// pages/my-posts/[userSignId].tsx

import { useRouter } from 'next/router';
import { useEffect, useState } from 'react';
// üí° API Ìï®Ïàò Ïù¥Î¶ÑÏùÑ 'getMyPosts' ÎåÄÏã† 'getPostsByAuthor'Î°ú ÏÇ¨Ïö©ÌïòÏó¨ ÏùºÍ¥ÄÏÑ±ÏùÑ Ïú†ÏßÄÌïòÍ±∞ÎÇò,
// ÏÇ¨Ïö©ÏûêÍ∞Ä Î™ÖÏãúÌïú 'getMyPosts'Î•º Í∑∏ÎåÄÎ°ú ÏÇ¨Ïö©ÌïúÎã§Í≥† Í∞ÄÏ†ïÌï©ÎãàÎã§.
import { getMyPosts } from '@/api/blogService/blog';
import { PostEntity } from '@/types/blogService/blogType';
import styles from '@/styles/blogService/MyPosts.module.css';

// üü¢ [Ï∂îÍ∞Ä] Layout Ïª¥Ìè¨ÎÑåÌä∏ ÏûÑÌè¨Ìä∏ (MainLayoutÏùÑ ÏÇ¨Ïö©ÌïúÎã§Í≥† Í∞ÄÏ†ï)
import Layout from "@/styles/layout/layout.module.css";


// Í≤åÏãúÍ∏Ä ÎØ∏Î¶¨Î≥¥Í∏∞ Ïª¥Ìè¨ÎÑåÌä∏ (PostPreview)Îäî Î≥ÄÍ≤Ω ÏóÜÏù¥ Ïú†ÏßÄÌï©ÎãàÎã§.
const PostPreview: React.FC<{ post: PostEntity }> = ({ post }) => {
  const router = useRouter();
  const handlePostClick = () => {
    router.push(`/post/${post.postId}`);
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('ko-KR');
  };

  return (
      <div className={styles.postPreview} onClick={handlePostClick}>
        {post.thumbnail && (
            <img
                src={post.thumbnail}
                alt={post.title}
                className={styles.thumbnail}
            />
        )}
        <div className={styles.postContent}>
          <h3 className={styles.postTitle}>{post.title}</h3>
          <p className={styles.postSnippet}>{post.content.substring(0, 100)}...</p>
          <div className={styles.postMeta}>
            <span>ÏûëÏÑ±Ïùº: {formatDate(post.createdAt)}</span>
            <span>Ï°∞ÌöåÏàò: {post.viewCount}</span>
          </div>
        </div>
      </div>
  );
};


// üí° [ÏàòÏ†ï] Î©îÏù∏ Î°úÏßÅÏùÑ MyPostsContent Ìï®ÏàòÎ°ú Î∂ÑÎ¶¨Ìï©ÎãàÎã§.
function MyPostsContent({ authorId }: { authorId: string | string[] | undefined }) {
  const router = useRouter();

  const finalAuthorId = Array.isArray(authorId) ? authorId[0] : authorId;

  const [posts, setPosts] = useState<PostEntity[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [currentPage, setCurrentPage] = useState(0);
  const [isLastPage, setIsLastPage] = useState(false);

  // currentUserIdÎäî Î°úÏßÅÏù¥ Î≥µÏû°ÌïòÏßÄ ÏïäÏúºÎØÄÎ°ú Í∑∏ÎåÄÎ°ú Ïú†ÏßÄ
  const currentUserId = typeof window !== "undefined" ? localStorage.getItem("userSignId") : null;

  useEffect(() => {
    if (!finalAuthorId || !router.isReady) { // üí° finalAuthorId ÏÇ¨Ïö©
      return;
    }

    const fetchPosts = async (page: number) => {
      if (page === 0) setIsLoading(true);
      setError(null);

      try {
        // API Ìò∏Ï∂ú (ÏÇ¨Ïö©ÏûêÍ∞Ä Ï†úÍ≥µÌïú ÏΩîÎìúÎ•º Í∏∞Î∞òÏúºÎ°ú getMyPosts Ïú†ÏßÄ)
        const response = await getMyPosts(finalAuthorId as string, page);

        // ÌéòÏù¥ÏßÄ 0Ïù¥Î©¥ Î™©Î°ùÏùÑ ÏÉàÎ°ú ÏÑ§Ï†ï, ÏïÑÎãàÎ©¥ Í∏∞Ï°¥ Î™©Î°ùÏóê Ï∂îÍ∞Ä
        setPosts(prev => page === 0 ? response.content : [...prev, ...response.content]);

        setIsLastPage(response.last);
      } catch (err) {
        setError("Í≤åÏãúÍ∏Ä Î™©Î°ùÏùÑ Î∂àÎü¨Ïò§Îäî Îç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
        console.error(err);
      } finally {
        setIsLoading(false);
      }
    };

    fetchPosts(currentPage);
  }, [finalAuthorId, router.isReady, currentPage]); // üí° finalAuthorIdÎ•º ÏùòÏ°¥ÏÑ± Î∞∞Ïó¥Ïóê Ï∂îÍ∞Ä

  const handleLoadMore = () => {
    if (!isLastPage && !isLoading) {
      setCurrentPage(prev => prev + 1);
    }
  };

  if (!finalAuthorId) { // üí° finalAuthorId ÏÇ¨Ïö©
    return <div className={styles.loading}>Ï†ïÎ≥¥Î•º Î∂àÎü¨Ïò§Îäî Ï§ë...</div>;
  }

  if (isLoading && currentPage === 0) {
    return <div className={styles.loading}>Í≤åÏãúÍ∏Ä Î™©Î°ù Î°úÎî© Ï§ë...</div>;
  }

  if (error) {
    return <div className={styles.error}>{error}</div>;
  }

  if (posts.length === 0) {
    return (
        <div className={styles.container}>
          <h1 className={styles.pageTitle}>{finalAuthorId}ÎãòÏùò Í≤åÏãúÎ¨º</h1>
          <p className={styles.noContent}>ÏûëÏÑ±Îêú Í≤åÏãúÍ∏ÄÏù¥ ÏóÜÏäµÎãàÎã§.</p>
        </div>
    );
  }

  // Ï†ïÏÉÅ Î†åÎçîÎßÅ
  return (
      <div className={styles.container}>
        <h1 className={styles.pageTitle}>
          {finalAuthorId}ÎãòÏùò Í≤åÏãúÎ¨º ({posts.length}Í∞ú)
        </h1>

        <div className={styles.postList}>
          {posts.map(post => (
              <PostPreview key={post.postId} post={post} />
          ))}
        </div>

        {!isLastPage && (
            <button
                onClick={handleLoadMore}
                disabled={isLoading}
                className={styles.loadMoreButton}
            >
              {isLoading ? 'Î°úÎî© Ï§ë...' : 'Îçî Î≥¥Í∏∞'}
            </button>
        )}
      </div>
  );
}


// üí° [ÏàòÏ†ï] export default Ìï®ÏàòÏóêÏÑú LayoutÏùÑ Ï†ÅÏö©Ìï©ÎãàÎã§.
export default function MyPostsPage() {
  const router = useRouter();
  const { userSignId } = router.query;

  return (
      // üü¢ Layout Ïª¥Ìè¨ÎÑåÌä∏Î°ú Í∞êÏã∏ÏÑú ÏÉÅÎã®Î∞î/Ìë∏ÌÑ∞ Îì± Í≥µÌÜµ ÏöîÏÜå Ï†ÅÏö©
      <Layout>
        <MyPostsContent authorId={userSignId} />
      </Layout>
  );
}